<template>
  <section class="tomato_section">
    <header class="tomato_header">우리 토마토</header>
    <apexchart  width="500" height="500" :options="chartOptions" :series="series" class="toamato_chart"></apexchart>
  </section>
</template>

<script>
import '@/assets/style/DetailStudy/tomato_rate.scss'
import {useStore} from 'vuex'
import { computed, reactive } from 'vue'

export default {
  name: 'TomatoRate',

  props: {
    studyId: {
      type: String,
      required: true,
    }
  },

  setup() {
    const store = useStore()
    const state = reactive({
      total_sum : computed(() => {
        // return store.state.totalTomato
        return store.state.temp_tomato_info.totalSum
      }),
      study_tomato : computed(() => {
        // return store.state.studyTomato
        return store.state.temp_tomato_info.relevantSum
      })
    })

    console.log(state.total_sum, state.study_tomato)


    let num = 0;
    if(!state.total_sum){
      num = 0;
    }else{
      num = Math.round(state.study_tomato/state.total_sum * 100)
    }
    const series =  [num]
    const chartOptions = {
      chart: {
        height: 350,
        type: 'radialBar',
      },
      colors: ["#E94846"],
      plotOptions: {
        radialBar: {
          // 그래프 시작점, 끝점 지정하기
          // startAngle: 0,
          // endAngle: 225,
            hollow: {
            margin: 0,
            size: '80%',  // 원 둘레 두께
            image: 'data:image/jpeg;base64,',
            imageWidth: 64,
            imageHeight: 64,
            imageClipped: true,
            position: 'front',
            // 그림자 기술 넣기
            dropShadow: {
              enabled: true,
              top: 3,
              left: 0,
              blur: 4,
              opacity: 0.24
            }
          },
          track: {
            background: '#fff',  // 원 둘레 중 안채워진 공간 색깔
            strokeWidth: '70%',
            margin: 0, // margin is in pixels
            dropShadow: {
              enabled: true,
              top: -3,
              left: 0,
              blur: 4,
              opacity: 0.35
            }
          },
      
          dataLabels: {
            show: true,
            name: {   // 여기 더 파보기 !!!!!!
              offsetY: -10,
              show: true,
              color: 'black',
              fontSize: '25px'
            },
            value: {
              color: 'black',
              fontSize: '50px',
              show: true,
            }
          }
        }
      },
      fill: {
        type: 'gradient',
        gradient: {
          shade: 'dark',
          // type: 'horizontal',
          type: 'vertical',
          shadeIntensity: 0.5,
          gradientToColors: ['#FFF96A'],  // 원 둘레의 색깔을 변경 가능하다
          inverseColors: true,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [0, 100]
        }
      },
      stroke: {
        lineCap: 'round'
      },
      labels: [''],  // 원 안에 내용 원하는거 작성하세요
    }

    // onBeforeMount(() => {
    //   store.dispatch('getStudyTomato', props.studyId)
    // })

    

    return {
      state,
      chartOptions,
      series,

    }
  }

}
</script>

